@import url(https://fonts.googleapis.com/css?family=Lato:300,400|Poppins:300,400,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
  box-sizing:border-box;

}
html{
  font-size: 10px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 4rem 2rem;
}
.app_search{
    margin-top: 1.8rem;

}
.products_input{
    padding: 1rem 1.6rem;
    min-width: 20rem;
    max-width: 100vw;
    font-style: 1.5rem;
}
.submit_product,.analyse_btn{
    padding: 0.6rem 1.6rem;
    font-size: 1.5rem;
    margin-left: 1rem;
    margin-right: 1rem;
    background-color: rgb(7, 7, 7);
    border: 2px solid ;
    border-radius: 4px ;
    color: white;
    transition: 0.25s;
    cursor: pointer;
}
.submit_product:hover,.analyse_btn:hover {
  animation: submit 1s ;
  box-shadow: 0 0 0 0.2em grey;
}

.analyse_btn{
    position:fixed;
    z-index: 99;
    bottom: 20px;
    right: 30px;
}
.exit_btn{
    z-index: 100;
    padding: 0.8rem 2.5rem;
}
body {
  font-family: "Poppins";
}

.container {
  width:auto;
  margin-left: 1rem;
  border: 2px solid black;
  display:inline-block;
}
.container .post {
  width: 35rem;
  height: 55rem;
  flex-direction: row;
  position: relative;
}

.container .post .header_post {
  width: 100%;
  height: 100%;
  background:white;
  position: absolute;
  top: 0;
  transition: cubic-bezier(0.68, -0.55, 0.27, 1.55) 320ms;
}
.container .post .header_post img {
  max-width: 100%;
  height: 90%;
}
.container .post .body_post {
  width: 100%;
  height: 60%;
  background: #fff;
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: cubic-bezier(0.68, -0.55, 0.27, 1.55) 320ms;
  cursor: pointer;
}
.container .post .body_post .post_content {
  width: 80%;
  height: 80%;
  background: #fff;
  position: relative;
  overflow: scroll;
}
.container .post .body_post .post_content h1 {
  font-size: 20px;
  font-weight: bold;
}
.container .post .body_post .post_content p {
  font-size: 14px;
  font-weight: normal;
  padding-bottom: 3rem;
}
.container .post .body_post  .container_infos {
  width: 100%;
  display: flex;
  justify-content: space-between;
    background-color: lightgrey;
  padding: 20px;
  position: absolute;
  bottom: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
.container .post .body_post  .container_infos .price {
  display: flex;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 12px;
}
.container .post .body_post  .container_infos .price span {
  font-size: 12px;
  text-transform: uppercase;
  opacity: 0.5;
  letter-spacing: 1px;
  font-weight: bold;
}
.container .post .body_post  .container_infos .container_tags {
  display: flex;
  flex-direction: column;
}
.container .post .body_post  .container_infos .container_tags span {
  font-size: 12px;
  text-transform: uppercase;
  opacity: 0.5;
  letter-spacing: 1px;
  font-weight: bold;
}
.container .post .body_post  .container_infos .container_tags .tags ul {
  display: flex;
}
.container .post .body_post  .container_infos .container_tags .tags ul li {
  font-size: 12px;
  letter-spacing: 2px;
  list-style: none;
  margin-left: 8px;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.container .post .body_post .post_content .container_infos .container_tags .tags ul li:first-child {
  margin-left: 0px;
}

@media (max-width:1200px) {
  .container .post{
    height: 50rem;
  }
  
}
